.signup-page {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
    position: relative;
}

.signup-container {
    background-color: #fff;
    width: 40%;
    min-height: 55%;
    height: auto;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0px 2px 10px #00000085;
    position: absolute;
    top: 0%;
    transition: translateY(-50%);
}

.form-container {
    padding: 0 30px 0;
}

.signup-title {
    font-weight: bold;
    padding: 15px 0 0 0;
}

@media (max-width: 1100px) {

    .signup-container {
        width: 45%;
    }
}

@media (max-width: 900px) {

    .signup-container {
        width: 50%;
    }
}

@media (max-width: 700px) {

    .signup-container {
        width: 70%;
    }
}

@media (max-width: 600px) {

    .signup-container {
        width: 75%;
    }
}

@media (max-width: 450px) {

    .signup-container {
        width: 80%;
    }
    
    .form-container {
        padding: 20px
    }
}

@media (max-width: 350px) {

    .form-container {
        padding: 5px
    }
}

